// CONVERTING PX TO REM
@function rem($size, $default-size: $default-font-size) {
	@return ($size / $default-size) + rem; }


// Dark / Light mode
@mixin dark {
	body.dark {
		@content; } }


// Responsive breakpoints
@mixin r($width) {
	@media only screen and (max-width: $width+ "px") {
		@content; } }

@mixin rmin($width) {
	@media only screen and (min-width: $width+ "px") {
		@content; } }

@mixin xxl {
	@include rmin(1400) {
		@content; } }

@mixin xl {
	@include rmin(1200) {
		@content; } }

@mixin lg {
	@include rmin(992) {
		@content; } }

@mixin md {
	@include rmin(768) {
		@content; } }

@mixin sm {
	@include rmin(576) {
		@content; } }
